<template>
  <ec-container height="100%" color-code="001" class="info" title="基地单元衔接" show-title>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="onQuery" @reset="resetHandle">
            <ec-query-item label="单元属性">
              <ec-select
                showSearch
                allowClear
                placeholder="请选择单元属性"
                v-model.trim="query.unitProp"
              >
                <a-select-option v-for="item in unitPropList" :key="item.enumKey">
                  {{ item.name }}
                </a-select-option>
              </ec-select>
            </ec-query-item>
            <ec-query-item label="状态">
              <ec-select
                showSearch
                allowClear
                placeholder="请选择状态"
                v-model.trim="query.billState"
              >
                <a-select-option v-for="item in billStateList" :key="item.enumKey">
                  {{ item.name }}
                </a-select-option>
              </ec-select>
            </ec-query-item>
            <ec-query-item label="基地单元名称">
              <a-input v-model.trim="query.baseCellName" placeholder="请输入基地单元名称" />
            </ec-query-item>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout :auto="false">
            <!-- 表格工具栏 -->
            <ec-v-item height="auto">
              <ec-panel-toolbar>
                <ec-panel-item-left>
                  <a-button type="primary" @click="handleUpdate('ADD')">新建单元</a-button>
                  <a-button type="primary" ghost @click="handleUpdate('EXISTING')">
                    已有单元
                  </a-button>
                  <a-button type="primary" ghost @click="onDelete"> 删除 </a-button>
                </ec-panel-item-left>
                <ec-panel-item-right>
                  <div class="unit-box">
                    <span>集中交易年度：{{ busiYear }}年</span>
                    <div class="select">
                      重量单位：
                      <ec-select showSearch v-model.trim="weightUnit">
                        <a-select-option v-for="item in weightUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                    <div class="select">
                      面积单位：
                      <ec-select showSearch v-model.trim="areaUnit">
                        <a-select-option v-for="item in areaUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                  </div>
                </ec-panel-item-right>
              </ec-panel-toolbar>
            </ec-v-item>
            <ec-v-item height="auto">
              <div class="side-info">
                <p>
                  <strong>辅助信息</strong>国家局设置新建单元数据为：<strong>{{
                    auxiliaryInfo.baseUnitCnt
                  }}</strong
                  >个，已增加：<strong>{{ auxiliaryInfo.baseUnitCntSet }}</strong
                  >个，剩余：<strong>{{ auxiliaryInfo.baseUnitCntUnSet }}</strong
                  >个。
                </p>
              </div>
            </ec-v-item>
            <!-- 列表 -->
            <ec-v-item>
              <ec-table
                ref="tableRef"
                :loading="loading"
                show-overflow
                height="100%"
                :auto="false"
                @page-action="queryHandle"
                :pagination="pagination"
                :data="tableData"
                @checkbox-all="onSelAllTableData"
                @checkbox-change="onSelTableData"
              >
                <ec-table-col type="checkbox" width="60" align="center"></ec-table-col>
                <!-- width 设置当前列的宽度 sortable 是否可排序 -->
                <ec-table-col type="seq" title="序号" width="50" align="center" />
                <ec-table-col
                  field="linkInduProvOrgName"
                  title="省级工业公司"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="linkInduOrgName"
                  title="对口工业企业"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="provOrgName" title="省公司" min-width="150" align="left" />
                <ec-table-col field="cityOrgName" title="市公司" min-width="150" align="left" />
                <ec-table-col field="provDivisionName" title="省" min-width="150" align="left" />
                <ec-table-col
                  field="cityDivisionName"
                  title="地(州、市)"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="counDivisionName"
                  title="县(区、市)"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="baseCellCode"
                  title="基地单元编码"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="baseCellName"
                  title="基地单元名称"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="brandName" title="品牌" min-width="150" align="left" />
                <ec-table-col field="leafTypeCode" title="烟叶类型" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.leafTypeCode | getNameBy(leafTypeObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="leafVarietyCode" title="烟叶品种" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.leafVarietyCode | getNameBy(leftVarietyObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="fieldArea" title="基本烟田面积" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.fieldArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="plantArea" title="种植面积" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.plantArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="buyWeight" title="收购量" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.buyWeight | amountFormat(weightUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  field="techSuppOrg"
                  title="技术依托单位"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="unitProp" title="单元属性" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{
                        row.unitProp
                          | getNameBy(unitPropList, {
                            key: 'enumKey',
                            label: 'name',
                          })
                      }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="linkUpState" title="衔接状态" width="100" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.linkUpState | getNameBy(linkUpStateObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="billState" title="状态" width="100" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.billState | getNameBy(billStateObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col title="操作" width="100" align="center">
                  <template #default="{ row }">
                    <a-button type="link" @click="handleDetail(row)">详情</a-button>
                  </template>
                </ec-table-col>
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <business-org-manage-edit
        ref="editRef"
        :busiYear="busiYear"
        :auxiliaryInfo="auxiliaryInfo"
        :weightUnitList="weightUnitList"
        :areaUnitList="areaUnitList"
        :weightUnit="weightUnit"
        :areaUnit="areaUnit"
        :leafTypeList="leafTypeList"
        :linkUpStateList="linkUpStateList"
        :userInfo="appConfig"
        :businessOrgList="businessOrgList"
        @saved="onQuery"
      />
      <detail-modal
        ref="detailModalRef"
        :busiYear="busiYear"
        :leafTypeList="leafTypeList"
        :userInfo="appConfig"
      />
    </ec-container-body>
  </ec-container>
</template>

<script>
import BusinessOrgManageEdit from './connectEdit.vue';
import DetailModal from './DetailModal.vue';

export default {
  name: 'PanelSupple',
  components: {
    BusinessOrgManageEdit,
    DetailModal,
  },
  data() {
    return {
      loading: false,
      query: {}, // 查询数据
      // 重量单位
      weightUnitList: [],
      // 面积单位
      areaUnitList: [],
      // 当前重量单位
      weightUnit: '500000',
      // 当前面积单位
      areaUnit: '10000',
      // 单元属性
      unitPropList: [],
      // 基地单元名称
      billStateList: [],
      // 辅助信息
      auxiliaryInfo: {},
      tableData: [], // 表格数据
      pagination: {
        // 分页
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 烟叶类型列表
      leafTypeList: [],
      // 烟叶品种
      leafVarietyList: [],
      // 合作关系
      linkUpStateList: [],
      // 选中列表数据
      selTableData: [],
      // 商业单位
      businessOrgList: [],
    };
  },
  computed: {
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
    leafTypeObj() {
      return this.leafTypeList.map((item) => {
        return {
          key: item.leafTypeCode,
          title: item.leafTypeName,
        };
      });
    },
    leftVarietyObj() {
      return this.leafVarietyList.map((item) => {
        return {
          key: item.leafVarietyCode,
          title: item.leafVarietyName,
        };
      });
    },
    linkUpStateObj() {
      return this.linkUpStateList.map((item) => {
        return {
          key: item.enumKey,
          title: item.name,
        };
      });
    },
    billStateObj() {
      return this.billStateList.map((item) => {
        return {
          key: item.enumKey,
          title: item.name,
        };
      });
    },
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
    bizCode() {
      return this.$store.getters.getAppConfig?.bizCode;
    },
  },
  created() {
    // 通过路由获取appCode
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM';
    this.query.busiYear = this.busiYear;
    this.handleGetEnum();
    this.onGet();
    this.handleQueryLeafTypeList();
    this.handleQueryLeafVarietyList();
    this.handleQueryBusinessOrgList();
  },
  methods: {
    // 查询枚举值
    handleGetEnum() {
      const params = {
        enumTypeCodes: [
          'WEIGHT_UNIT',
          'AREA_UNIT',
          'BASE_UNIT_NATURE',
          'LINK_UP_STATE',
          'BILL_STATE',
        ],
      };
      this.$apis.bim.enumType.query(params).then((res) => {
        const enumMap = res?.enumMap || {};
        this.weightUnitList = enumMap.WEIGHT_UNIT || [];
        this.areaUnitList = enumMap.AREA_UNIT || [];
        this.unitPropList = enumMap.BASE_UNIT_NATURE || [];
        this.linkUpStateList = enumMap.LINK_UP_STATE || [];
        this.billStateList = enumMap.BILL_STATE || [];
      });
    },
    // 查询烟叶类型
    handleQueryLeafTypeList() {
      this.$apis.bim.leafType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
      });
    },
    // 查询烟叶品种
    handleQueryLeafVarietyList() {
      this.$apis.bim.leftVariety.query({}).then((res) => {
        this.leafVarietyList = res?.rows || [];
      });
    },
    // 获取辅助信息
    onGet() {
      this.$apis.apm.baseConnect.get().then((res) => {
        this.auxiliaryInfo = res || {};
      });
    },
    // 重置
    resetHandle() {
      this.query = {
        busiYear: this.busiYear,
      };
      this.onQuery();
    },
    // 查询
    onQuery() {
      this.$refs.tableRef.pageAction(1, 10);
    },
    // 分页查询
    queryHandle(pageNumber, pageSize) {
      const params = {
        pageNo: pageNumber,
        pageSize,
        ...this.query,
      };
      this.loading = true;
      this.$apis.apm.baseConnect
        .query(params)
        .then((res) => {
          this.loading = false;
          this.tableData = res?.rows || [];
          this.pagination.total = res?.totalRows;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 新增/编辑
    handleUpdate(type) {
      this.$refs.editRef.show(type);
    },
    editRowEvent() {
      const $table = this.$refs.xTable;
      $table.setActiveRow();
    },
    // 详情
    handleDetail(data) {
      this.$refs.detailModalRef.show(data);
    },
    // 删除
    onDelete() {
      if (!this.selTableData.length) {
        this.$message.error('请选择一条数据再删除');
        return;
      }
      const flag = this.selTableData.some((item) => {
        return item.billState !== '1' && item.billState !== '3' && item.billState !== '5';
      });
      if (flag) {
        this.$message.error('只有退回状态才能删除,请重新选择！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认删除选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          const params = {
            bmUnitLinkRecIds: this.selTableData.map((item) => item.bmUnitLinkRecId),
          };
          this.loading = true;
          this.$apis.apm.baseConnect.delete(params).then(() => {
            this.loading = false;
            this.$message.error('删除成功');
            this.onQuery();
          });
        },
      });
    },
    // table选中对应项
    onSelTableData({ records }) {
      this.selTableData = records;
    },
    // table选中所有项
    onSelAllTableData({ records }) {
      this.selTableData = records;
    },
    // 查询商业单位
    async handleQueryBusinessOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'TOBACCO',
        orgLevelEk: '2',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.businessOrgList = res.rows || [];
    },
  },
};
</script>
<style lang="less" scoped>
.info {
  .unit-select {
    & > span {
      margin-right: 10px;
      color: #18191a;
      font-size: 16px;
      font-weight: bold;
    }
    /deep/.ec-query-item__label {
      color: #18191a;
      font-size: 16px;
      font-weight: bold;
    }
  }
  .side-info {
    padding: 4px 10px;
    border-radius: 4px;
    border: 1px solid #d5f0ff;
    background: #e6f7ff;
    p {
      margin-bottom: 0;
      color: #8e9496;
      font-size: 14px;
      strong {
        color: #3092fe;
      }
    }
  }
}
/deep/ .ec-panel-item .ec-panel-item__right {
  display: flex;
  align-items: center;
}
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
