<template>
  <div>
    <ec-drawer
      :title="title"
      :visible="visible"
      size="large"
      @close="handleCancel"
      :is-confirm="true"
    >
      <div class="unit-box">
        <div>
          重量单位:
          <ec-select showSearch v-model.trim="weightUnit">
            <a-select-option v-for="item in weightUnitList" :key="item.enumKey">
              {{ item.name }}
            </a-select-option>
          </ec-select>
        </div>
        <div>
          面积单位:
          <ec-select showSearch v-model.trim="areaUnit">
            <a-select-option v-for="item in areaUnitList" :key="item.enumKey">
              {{ item.name }}
            </a-select-option>
          </ec-select>
        </div>
      </div>
      <ec-container height="100%" v-show="steps === 0">
        <ec-container-body>
          <ec-panel :auto="false">
            <ec-panel-toolbar>
              <ec-query-panel :defaultFold="true" @query="onQuery" @reset="resetHandle">
                <ec-query-item label="集中交易年度">
                  <a-input-number
                    placeholder="请输入集中交易年度"
                    v-model.trim="query.busiYear"
                    disabled
                  />
                </ec-query-item>
                <ec-query-item label="商业单位">
                  <select-picker
                    title="选择商业单位"
                    placeholder="请输入商业单位"
                    :lazy="false"
                    :search="false"
                    :fetchData="onFetchBusinessOrgList"
                    v-model="query.businesOrgCode"
                  />
                </ec-query-item>
                <ec-query-item label="基地单元名称">
                  <a-input v-model.trim="query.baseCellName" placeholder="请输入基地单元名称" />
                </ec-query-item>
                <template #inner>
                  <ec-query-item v-if="type === 'EXISTING'" label="合作关系">
                    <ec-select
                      showSearch
                      v-model.trim="query.linkUpState"
                      placeholder="请选择合作关系"
                    >
                      <a-select-option v-for="item in linkUpStateList" :key="item.enumKey">
                        {{ item.name }}
                      </a-select-option>
                    </ec-select>
                  </ec-query-item>
                  <ec-query-item v-if="type === 'EXISTING'" label="创建年度">
                    <a-input-number
                      :precision="1"
                      :max="busiYear"
                      v-model.trim="query.createYear"
                      placeholder="请输入创建年度"
                    />
                  </ec-query-item>
                </template>
              </ec-query-panel>
              <ec-divider />
            </ec-panel-toolbar>
            <ec-panel-body>
              <ec-v-layout :auto="false">
                <ec-v-item height="auto">
                  <div class="side-info">
                    <p>
                      <strong>辅助信息</strong>国家局设置新建单元数据为：<strong>{{
                        auxiliaryInfo.baseUnitCnt
                      }}</strong
                      >个，已增加：<strong>{{ auxiliaryInfo.baseUnitCntSet }}</strong
                      >个，剩余：<strong>{{ auxiliaryInfo.baseUnitCntUnSet }}</strong
                      >个。
                    </p>
                  </div>
                </ec-v-item>
                <!-- 列表 -->
                <ec-v-item>
                  <ec-table
                    ref="tableRef"
                    :loading="loading"
                    height="100%"
                    :auto="false"
                    show-overflow
                    @page-action="queryHandle"
                    :pagination="pagination"
                    :data="tableData"
                    highlight-current-row
                    @current-change="currentChangeEvent"
                  >
                    <ec-table-col type="seq" title="序号" width="50" align="center" />
                    <ec-table-col field="provOrgName" title="省公司" min-width="150" align="left" />
                    <ec-table-col field="cityOrgName" title="市公司" min-width="150" align="left" />
                    <ec-table-col
                      field="provDivisionName"
                      title="省"
                      min-width="150"
                      align="left"
                    />
                    <ec-table-col
                      field="cityDivisionName"
                      title="地(州、市)"
                      min-width="150"
                      align="left"
                    />
                    <ec-table-col
                      field="counDivisionName"
                      title="县(区、市)"
                      min-width="150"
                      align="left"
                    />
                    <ec-table-col
                      field="baseCellCode"
                      title="基地单元编码"
                      min-width="150"
                      align="left"
                    />
                    <ec-table-col
                      field="baseCellName"
                      title="基地单元名称"
                      min-width="150"
                      align="left"
                    />
                    <ec-table-col
                      field="leafTypeCode"
                      title="烟叶类型"
                      min-width="150"
                      align="left"
                    >
                      <template #default="{ row }">
                        <div>
                          {{
                            row.leafTypeCode
                              | getNameBy(leafTypeList, {
                                key: 'leafTypeCode',
                                label: 'leafTypeName',
                              })
                          }}
                        </div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="fieldArea"
                      title="基本烟田面积"
                      min-width="150"
                      align="left"
                    >
                      <template #default="{ row }">
                        <div>{{ row.fieldArea | amountFormat(areaUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col field="plantArea" title="种植面积" min-width="150" align="left">
                      <template #default="{ row }">
                        <div>{{ row.plantArea | amountFormat(areaUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col field="buyWeight" title="收购量" min-width="150" align="left">
                      <template #default="{ row }">
                        <div>{{ row.buyWeight | amountFormat(weightUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="techSuppOrg"
                      title="技术依托单位"
                      min-width="150"
                      align="left"
                    />
                    <ec-table-col
                      field="linkUpState"
                      title="衔接状态"
                      width="100"
                      align="left"
                      #default="{ row }"
                    >
                      {{
                        row.linkUpState === '0' ? '未衔接' : row.linkUpState === '1' ? '已衔接' : ''
                      }}
                    </ec-table-col>
                  </ec-table>
                </ec-v-item>
              </ec-v-layout>
            </ec-panel-body>
          </ec-panel>
        </ec-container-body>
        <ec-container-footer>
          <a-button
            :disabled="selRow.bmUnitLinkRecId || selRow.bmUnitId ? false : true"
            type="primary"
            @click="handleNext(1)"
            >下一步</a-button
          >
          <a-button @click="handleCancel">关闭</a-button>
        </ec-container-footer>
      </ec-container>
      <ec-container height="100%" v-show="steps === 1">
        <ec-container-body>
          <ec-panel title="基地单元基本信息" color-code="001">
            <ec-panel-item>
              <ec-form ref="ruleForm" :model="formData" :rules="rules">
                <a-row type="flex" :gutter="12">
                  <a-col :span="6">
                    <a-form-model-item label="省公司">
                      <a-input
                        disabled
                        v-model="formData.provOrgName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="所在行政区域">
                      <a-input
                        disabled
                        v-model="formData.counOrgName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="省">
                      <a-input
                        disabled
                        v-model="formData.provDivisionName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="地(州、市)">
                      <a-input
                        disabled
                        v-model="formData.cityDivisionName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="县(区、市)">
                      <a-input
                        disabled
                        v-model="formData.counDivisionName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="基地单元名称">
                      <a-input
                        disabled
                        v-model="formData.baseCellName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="烟叶类型">
                      <ec-select
                        disabled
                        placeholder="请选择烟叶类型"
                        v-model="formData.leafTypeCode"
                      >
                        <a-select-option v-for="item in leafTypeList" :key="item.leafTypeCode">
                          {{ item.leafTypeName }}
                        </a-select-option>
                      </ec-select>
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="技术依托单位">
                      <a-input
                        disabled
                        v-model="formData.techSuppOrg"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="基本烟田面积">
                      <a-input
                        disabled
                        :value="(Number(formData.fieldArea) / Number(areaUnit)).toFixed(2)"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="种植面积">
                      <a-input
                        disabled
                        :value="(Number(formData.plantArea) / Number(areaUnit)).toFixed(2)"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="收购量">
                      <a-input
                        disabled
                        :value="(Number(formData.buyWeight) / Number(weightUnit)).toFixed(2)"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="创建年度">
                      <a-input
                        disabled
                        v-model="formData.busiYear"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                </a-row>
              </ec-form>
            </ec-panel-item>
          </ec-panel>
          <ec-panel title="衔接信息" color-code="001">
            <ec-panel-item>
              <ec-form ref="ruleForm" :model="formData" :rules="rules">
                <a-row type="flex" :gutter="12">
                  <a-col :span="6">
                    <a-form-model-item label="单位名称">
                      <a-input
                        disabled
                        v-model="userInfo.orgName"
                        placeholder=""
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="品牌" prop="brandCode">
                      <ec-select allowClear placeholder="请选择品牌" v-model="formData.brandCode">
                        <a-select-option v-for="item in brandList" :key="item.brandCode">
                          {{ item.brandName }}
                        </a-select-option>
                      </ec-select>
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="6">
                    <a-form-model-item label="烟叶品种" prop="leafVarietyCode">
                      <ec-select
                        allowClear
                        placeholder="请选择烟叶品种"
                        v-model="formData.leafVarietyCode"
                      >
                        <a-select-option
                          v-for="item in leftVarietyList"
                          :key="item.leafVarietyCode"
                        >
                          {{ item.leafVarietyName }}
                        </a-select-option>
                      </ec-select>
                    </a-form-model-item>
                  </a-col>
                </a-row>
              </ec-form>
            </ec-panel-item>
          </ec-panel>
        </ec-container-body>
        <ec-container-footer>
          <a-button type="primary" @click="handlePre(0)">上一步</a-button>
          <a-button type="primary" @click="handleUpdate()">保存</a-button>
          <a-button @click="handleCancel">关闭</a-button>
        </ec-container-footer>
      </ec-container>
    </ec-drawer>
  </div>
</template>
<script>
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'BusinessOrgManageEdit',
  components: {
    SelectPicker,
  },
  props: {
    busiYear: {
      type: Number,
      default: 0,
    },
    auxiliaryInfo: {
      type: Object,
      default: () => {},
    },
    userInfo: {
      type: Object,
      default: () => {},
    },
    weightUnitList: {
      type: Array,
      default: () => [],
    },
    areaUnitList: {
      type: Array,
      default: () => [],
    },
    leafTypeList: {
      type: Array,
      default: () => [],
    },
    linkUpStateList: {
      type: Array,
      default: () => [],
    },
    businessOrgList: {
      type: Array,
      default: () => [],
    },
    weightUnit: {
      type: String,
      default: '',
    },
    areaUnit: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      visible: false,
      loading: false,
      query: {},
      formData: {},
      rules: {
        brandCode: { required: true, message: '请选择品牌', trigger: 'change' },
        leafVarietyCode: { required: true, message: '请选择烟叶瓶子', trigger: 'change' },
      },
      tableData: [],
      pagination: {
        // 分页
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 当前处于第几页
      steps: 0,
      // 新建类型
      type: '',
      // 当前选中table行
      selRow: {},
      // 品牌列表
      brandList: [],
      // 烟叶品种列表
      leftVarietyList: [],
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
    };
  },
  computed: {
    title() {
      return `新增基地单元衔接(${this.type === 'ADD' ? '新建单元' : '已有单元'})`;
    },
  },
  methods: {
    show(type) {
      this.type = type;
      this.visible = true;
      this.$set(this.query, 'busiYear', this.busiYear);
      if (this.type === 'EXISTING') {
        this.$set(this.query, 'linkUpState', '0');
      }
      this.onQuery();
    },
    // 更新商业单位
    onFetchBusinessOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.businessOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 重置
    resetHandle() {
      this.query = {
        busiYear: this.busiYear,
      };
    },
    // 查询烟叶品种
    handleQueryLeafVarietyList() {
      const params = {
        leafTypeCode: this.formData.leafTypeCode,
      };
      this.$apis.bim.leftVariety.query(params).then((res) => {
        this.leftVarietyList = res?.rows || [];
      });
    },
    // 查询品牌列表
    handleQueryBrandList() {
      const params = {
        induOrgCode: this.userInfo.orgCode,
      };
      this.$apis.apm.baseConnect.brandQuery(params).then((res) => {
        this.brandList = res?.rows || [];
      });
    },
    // 查询
    onQuery() {
      this.$refs?.tableRef?.pageAction(1, 10);
    },
    // 查询
    queryHandle(pageNumber, pageSize) {
      const params = {
        pageNo: pageNumber,
        pageSize,
        ...this.query,
      };
      this.loading = true;
      if (this.type === 'ADD') {
        this.$apis.apm.baseConnect
          .addQuery(params)
          .then((res) => {
            this.loading = false;
            this.tableData = res?.rows || [];
            this.pagination.total = res?.totalRows;
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        this.$apis.apm.baseConnect
          .existingQuery(params)
          .then((res) => {
            this.loading = false;
            this.tableData = res?.rows || [];
            this.pagination.total = res?.totalRows;
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
    // 选中当前行
    currentChangeEvent({ row }) {
      this.selRow = row;
    },
    // 下一步
    handleNext(num) {
      this.steps = num;
      this.formData = this.selRow;
      this.handleQueryLeafVarietyList();
      this.handleQueryBrandList();
      this.$set(this.formData, 'brandCode', undefined);
      this.$set(this.formData, 'leafVarietyCode', undefined);
    },
    handlePre(num) {
      this.steps = num;
    },
    // 新增/编辑
    handleUpdate() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.formData.brandName = this.brandList.find(
            (item) => item.brandCode === this.formData.brandCode,
          )?.brandName;
          if (this.type === 'ADD') {
            this.$apis.apm.baseConnect.addSave(this.formData).then(() => {
              this.$message.success('保存成功！');
              this.handleCancel();
              this.$emit('saved');
            });
          } else {
            this.$apis.apm.baseConnect.existingSave(this.formData).then(() => {
              this.$message.success('保存成功！');
              this.handleCancel();
              this.$emit('saved');
            });
          }
        } else {
          this.$message.error('验证不通过');
        }
      });
    },
    // 取消
    handleCancel() {
      this.visible = false;
      this.steps = 0;
      this.resetHandle();
      this.formData = {};
      this.$refs.ruleForm.resetFields();
      this.tableData = [];
    },
  },
};
</script>
<style lang="less" scoped>
.side-info {
  padding: 4px 10px;
  border-radius: 4px;
  border: 1px solid #d5f0ff;
  background: #e6f7ff;
  p {
    margin-bottom: 0;
    color: #8e9496;
    font-size: 14px;
    strong {
      color: #3092fe;
    }
  }
}
.unit-box {
  position: absolute;
  right: 40px;
  top: 6px;
  display: flex;
  div {
    margin-left: 16px;
    /deep/ .ant-select {
      width: 150px;
    }
  }
}
</style>
