<template>
  <ec-modal v-model="visibile" title="详细" width="1000px" :is-confirm="true">
    <ec-form :model="formData" :rules="rules">
      <ec-panel title="基地单元基本信息" color-code="001">
        <ec-panel-item>
          <a-row type="flex" :gutter="12">
            <a-col :span="6">
              <a-form-model-item label="省公司">
                <a-input disabled v-model="formData.provOrgName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="所在行政区域">
                <a-input disabled v-model="formData.counOrgName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="省">
                <a-input disabled v-model="formData.provDivisionName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="地(州、市)">
                <a-input disabled v-model="formData.cityDivisionName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="县(区、市)">
                <a-input disabled v-model="formData.counDivisionName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="基地单元名称">
                <a-input disabled v-model="formData.baseCellName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="烟叶类型">
                <ec-select disabled v-model="formData.leafTypeCode">
                  <a-select-option v-for="item in leafTypeList" :key="item.leafTypeCode">
                    {{ item.leafTypeName }}
                  </a-select-option>
                </ec-select>
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="技术依托单位">
                <a-input disabled v-model="formData.techSuppOrg" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="基本烟田面积">
                <a-input
                  disabled
                  :value="(Number(formData.fieldArea) / Number(10000)).toFixed(2)"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="种植面积">
                <a-input
                  disabled
                  :value="(Number(formData.plantArea) / Number(10000)).toFixed(2)"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="收购量">
                <a-input
                  disabled
                  :value="(Number(formData.buyWeight) / Number(500000)).toFixed(2)"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="创建年度">
                <a-input disabled v-model="formData.busiYear" />
              </a-form-model-item>
            </a-col>
          </a-row>
        </ec-panel-item>
      </ec-panel>
      <ec-panel title="衔接信息" color-code="001">
        <ec-panel-item>
          <a-row type="flex" :gutter="12">
            <a-col :span="6">
              <a-form-model-item label="单位名称">
                <a-input disabled v-model="userInfo.orgName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="品牌">
                <a-input disabled v-model="formData.brandName" />
              </a-form-model-item>
            </a-col>
            <a-col :span="6">
              <a-form-model-item label="烟叶品种">
                <ec-select disabled v-model="formData.leafVarietyCode">
                  <a-select-option v-for="item in leftVarietyList" :key="item.leafVarietyCode">
                    {{ item.leafVarietyName }}
                  </a-select-option>
                </ec-select>
              </a-form-model-item>
            </a-col>
          </a-row>
        </ec-panel-item>
      </ec-panel>
    </ec-form>
    <template v-slot:footer>
      <a-button @click="onClose">关闭</a-button>
    </template>
  </ec-modal>
</template>

<script>
export default {
  name: 'DetailModal',
  props: {
    leafTypeList: {
      type: Array,
      default: () => [],
    },
    busiYear: {
      type: Number,
      default: 0,
    },
    userInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      visibile: false,
      formData: {},
      rules: {},
      leftVarietyList: [],
      brandList: [],
    };
  },
  methods: {
    show(data) {
      this.visibile = true;
      this.formData = data;
      this.handleQueryLeafVarietyList();
      this.handleQueryBrandList();
    },
    onClose() {
      this.visibile = false;
    },
    // 查询烟叶品种
    handleQueryLeafVarietyList() {
      const params = {
        leafTypeCode: this.formData.leafTypeCode,
      };
      this.$apis.bim.leftVariety.query(params).then((res) => {
        this.leftVarietyList = res?.rows || [];
      });
    },
  },
};
</script>
